import React, { Component } from "react";
import { Container, Row, Col, Button, Form } from "react-bootstrap";
import Collapse from "rc-collapse";
import "rc-collapse/assets/index.css";
import CartDetails from "./CartDetails";
import CompletedOrder from "./CompletedOrder";
import history from "../history";

import { getLocalStorageObject } from "./Utilities";
import JWT from "../services/jwtService";
import Cookies from "universal-cookie";
import jwtDecode from "jwt-decode";
import cities from "../cities.json";

// ... REDUX ...
import withReducer from "../store/withReducer";
import connect from "react-redux/es/connect/connect";
import { bindActionCreators } from "redux";
import reducer from "./store/reducers";
import * as Actions from "./store/actions";

// import Login from './authorization/login/LoginForm'

import Login from "./LoginPopup";
import OTP from "./OTPPopup";
import Register from "./RegisterPopup";
import { v4 as uuid4 } from "uuid";
import { hostUrl } from "./store/config";

var Panel = Collapse.Panel;

class CheckoutPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      itemCount: 0,
      me: {},
      canSubmit: false,
      shortlist: false,
      showLogin: false,
      showOTP: false,
      showRegister: false,
      reRender: 1,
      reload: false,
      step1: false,
      step2: false,
      step3: false,
      step4: false,
      collapseActive: "1",
      value: 1,
      data: [],
      zip: false,
      procache: [],
      cookie: [],
      total: 0,
      guest: false,
      otp: false,
      summary: {
        totalValue: 0,
        discount: 0,
        shipping_charges: 0,
        estimated_gst: 0,
        final_payment: 0,
      },
      orderMessage: null,
      paymentMethod: 0,
      paymentComplete: false,
    };
  }

  componentDidMount() {
    console.log(this.props.history.location.hash, "history");
    if (this.props.history.location.hash.includes("hc-action-complete")) {
      this.setState({ paymentComplete: true });
      console.log("here");
      console.log(localStorage);
      let coupon = localStorage.getItem("coupon") || null;
      if (typeof coupon === "undefined") {
        console.log(coupon, "null");
      }
      this.successPayment(
        "4",
        true,
        coupon,
        JSON.parse(localStorage.getItem("cart_summary"))
      );
      return;
    }
    if (localStorage.getItem("gtToken")) {
      localStorage.removeItem("gtToken");
      localStorage.removeItem("_me");
    }

    if (Object.keys(getLocalStorageObject("procache")).length > 0) {
      let me = {};
      if (localStorage.getItem("_me")) {
        me = jwtDecode(localStorage.getItem("_me"));
        // console.log('ME :: ' + JSON.stringify(me));

        this.setState({
          me: me,
        });
      }
      this.props.getShipping();
      // else{
      //     this.handleShowLogin();
      // }
      this.props.getCartSummary(
        this.props.coupon_details ? this.props.coupon_details : {},
        this.props.shipping && this.props.shipping
      );
    } else {
      history.push({
        pathname: "/",
      });
      this.props.showResponseMessage(
        "Your Cart is empty, Please add items to the cart and then proceed to checkout",
        "info"
      );
    }
  }

  static getDerivedStateFromProps(props, state) {
    // console.log('Guest we have in gdfs :: ' + JSON.stringify(props.guest_obj))
    return {
      itemCount: props.itemCount,
      reRender: props.reRender,
      userProfile: props.userProfile,
      orderMessage: props.orderMessage,
      order_summary: props.order_summary,
      cart_summary: props.cart_summary,
      data: props.data,
      coupon_applied: props.coupon_applied,
      couponMsg: props.couponMsg,
      coupon_details: props.coupon_details,
      shipping: props.shipping,
      subTotal: props.subTotal,
      guest_variant: props.guest_variant,
      guest_msg: props.guest_msg,
      guest_obj: props.guest_obj,
    };
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevProps.guest_obj !== this.state.guest_obj) {
      // if(localStorage.getItem('gtToken'))
      // {
      let me = {};
      me = jwtDecode(localStorage.getItem("_me"));
      // alert('ME in CDU:: ' + JSON.stringify(me));

      // alert('Yes there is guest')
      // this.setState ({collapseActive: "2", step1: true })
      this.setState({
        me: me,
        collapseActive: "3",
        step2: true,
      });
      this.props.getShipping();
      // }
    }
    console.log(prevState.shipping, "prev");
    if (
      prevState.shipping !== this.props.shipping ||
      prevState.coupon_details !== this.props.coupon_details
    ) {
      this.props.getCartSummary(
        this.props.coupon_details ? this.props.coupon_details : {},
        this.props.shipping && this.props.shipping
      );
    }
  }

  // componentWillUnmount() {
  //     if(localStorage.getItem('gtToken'))
  //     {
  //         localStorage.removeItem('gtToken')
  //         localStorage.removeItem('_me')
  //     }
  // }

  // componentDidUpdate(prevProps, prevState) {
  //     console.log('Guest we have state :: ' + JSON.stringify(this.state.guest_obj))
  //     console.log('Guest we have props :: ' + JSON.stringify(this.props.guest_obj))
  //     if(prevState.guest_obj !==  this.state.guest_obj)
  //     {

  //     }

  //     if(prevState.step1 !== this.state.step1 || prevState.step2 !== this.state.step2)
  //     {
  //         this.props.getCartSummary(this.props.coupon_details ? this.props.coupon_details : {}, this.props.shipping && this.props.shipping);
  //     }
  // }

  handleCloseRegister = () => {
    this.setState({ showRegister: false });
  };

  handleShowRegister = () => {
    this.setState({ showRegister: true });
  };

  handleCloseLogin = () => {
    this.setState({ showLogin: false });
  };

  handleShowLogin = () => {
    this.setState({ showLogin: true });
  };

  handleLogout = () => {
    this.props.Logout();
    this.props.removeUserData();
  };

  handlePop = (trigger) => {
    this.setState({
      showLogin: false,
      showRegister: false,
    });

    this.props.renderAction();
  };

  decrease = () => {
    if (this.state.value) {
      this.setState({
        value: this.state.value - 1,
      });
    }
  };

  increase = () => {
    this.setState({ value: this.state.value + 1 });
  };

  findProductById = (data, idToLookFor) => {
    for (var i = 0; i < Object.keys(data).length; i++) {
      if (data[i].Id === parseInt(idToLookFor)) {
        return data[i];
      }
    }
  };

  handleValue = (value) => {
    this.setState({ value: value });
  };

  handleTotal = (total) => {
    this.setState({ total: total });
  };

  handlePayment = (collapseActive, step3, coupon) => {
    // coupon && alert('Coupon in checkout page :: ' + JSON.stringify(coupon))
    //  this.setState({ collapseActive: collapseActive, step3: step3 }, () => {
    // this.state.cart_summary && alert('Cart Summary :: ' + JSON.stringify(this.state.cart_summary))
    // this.props.placeOrder(coupon, this.state.cart_summary);
    //});
    if (this.state.paymentMethod == 0) {
      console.log(this.state.order_summary, "order_summary");
      this.setState({ collapseActive: collapseActive, step3: step3 }, () => {
        // this.state.cart_summary && alert('Cart Summary :: ' + JSON.stringify(this.state.cart_summary))
        let payment_method = "op";
        this.props.placeOrder(coupon, this.state.cart_summary, payment_method);
      });
    } else {
      if (coupon) {
        localStorage.setItem("coupon", JSON.stringify(coupon));
      } else {
        localStorage.setItem("coupon", JSON.stringify({}));
      }
      localStorage.setItem(
        "cart_summary",
        JSON.stringify(this.state.cart_summary)
      );
      let order_id = `o-${uuid4()}`;
      console.log(this.state.me, "state me");
      let billingAddress = {
        city:
          this.state.me && this.state.me.cust_city
            ? this.state.me.cust_city
            : "",
        street:
          this.state.me &&
          this.state.me.cust_address &&
          this.state.me.cust_address.streetAddress
            ? this.state.me.cust_address.streetAddress
            : "",
        stateProvince:
          this.state.me &&
          this.state.me.cust_address &&
          this.state.me.cust_address.state
            ? this.state.me.cust_address.state
            : "",
        postcodeZip:
          this.state.me && this.state.me.cust_zip ? this.state.me.cust_zip : "",
        country:
          this.state.me &&
          this.state.me.cust_address &&
          this.state.me.cust_address.country
            ? this.state.me.cust_address.country.slice(0, 3).toUpperCase()
            : "",
      };
      console.log(billingAddress, "Billing");

      fetch(`${hostUrl}/api/customer/account/alfalahsession`)
        .then((response) => response.json())
        .then((result) => {
          let session_id = result.result.session.id;
          console.log(
            order_id,
            this.props.cart_summary.final_payment,
            session_id,
            "data"
          );
          window.showPaymentPage(
            order_id,
            this.props.cart_summary.final_payment,
            session_id,
            billingAddress
          );
        })
        .catch((error) => console.log("error", error));
    }
  };
  successPayment = (collapseActive, step3, coupon, cart_summary) => {
    console.log("successPayment");
    // coupon && alert('Coupon in checkout page :: ' + JSON.stringify(coupon))
    this.setState({ collapseActive: collapseActive, step3: step3 }, () => {
      // this.state.cart_summary && alert('Cart Summary :: ' + JSON.stringify(this.state.cart_summary))
      let payment_method = "op";
      this.props.placeOrder(coupon, cart_summary, payment_method);
      this.props.history.push({ hash: "" });
    });
  };

  proceedToStep2 = () => {
    this.setState({
      collapseActive: "3",
      step2: true,
    });
  };

  handleAddressChange = (event) => {
    let updatedUser = this.state.me;

    switch (event.target.name) {
      case "house":
        updatedUser.cust_address.house_no = event.target.value;
        // console.log('House no :: ' + event.target.value);
        break;

      case "street":
        updatedUser.cust_address.streetAddress = event.target.value;
        // console.log('street :: ' + event.target.value);
        break;

      case "pincode":
        updatedUser.cust_zip = event.target.value;
        // console.log('Pincode :: ' + event.target.value);
        break;

      case "city":
        updatedUser.cust_city = event.target.value;
        // console.log('City :: ' + event.target.value);
        break;

      case "state":
        updatedUser.cust_address.state = event.target.value;
        // console.log('State :: ' + event.target.value);
        break;

      default:
    }

    this.setState({
      me: updatedUser,
    });
  };

  handleAddress = () => {
    console.log(this.state.me, "ME");
    if (this.state.me && !this.state.me.cust_address) {
      this.props.showResponseMessage(
        "Kindly complete your address to proceed further ...",
        "danger"
      );
    } else if (
      this.state.me &&
      (this.state.me.cust_address.house_no === "" ||
        this.state.me.cust_address.streetAddress === "" ||
        this.state.me.cust_zip === "" ||
        this.state.me.cust_address.state === "")
    ) {
      this.props.showResponseMessage(
        "Kindly complete your address to proceed further ...",
        "danger"
      );
      // alert('Kindly complete your address to proceed further ...');
    } else {
      console.log(this.state.me, "ME");
      // alert('sdfe')
      let formBody = new FormData();
      let { country } = this.form;
      if (localStorage.getItem("jwtToken")) {
        let cust_id = jwtDecode(localStorage.getItem("jwtToken")).cust_id;
        formBody.append("cust_id", cust_id);
      } else if (localStorage.getItem("gtToken")) {
        let cust_id = jwtDecode(localStorage.getItem("gtToken")).cust_id;
        formBody.append("cust_id", cust_id);
      }

      formBody.append("house_no", this.state.me.cust_address.house_no);
      formBody.append("address", this.state.me.cust_address.streetAddress);
      formBody.append("pincode", this.state.me.cust_zip);
      formBody.append("city", this.state.me.cust_city);
      formBody.append("state", this.state.me.cust_address.state);
      formBody.append("country", country.value);

      this.props.updateAddress(formBody);
      // this.props.getCartSummary(this.state.coupon_details ? this.state.coupon_details : {}, this.state.shipping && this.state.shipping);
      this.setState({ collapseActive: "4", step3: true });

      JWT.dataEncrpytion(this.state.me);
    }
  };

  handleContactChange = (evt) => {
    if (evt.target.value && Object.keys(evt.target.value).length === 10) {
      this.setState({
        canSubmit: true,
      });
    } else {
      this.setState({
        canSubmit: false,
      });
    }
  };

  handleNameChange = (evt) => {
    if (evt.target.value && Object.keys(evt.target.value).length >= 3) {
      this.setState({
        canSubmit: true,
      });
    } else {
      this.setState({
        canSubmit: false,
      });
    }
  };

  submitGuest = () => {
    const cookies = new Cookies();

    if (cookies.get("_otp__")) {
      alert(
        "You cannot Verify your number yet, Please re-try after 5 - 10 minutes..."
      );
    } else {
      if (!this.state.canSubmit) {
        alert(
          "Please Enter the correct Mobile number and complete name before proceeding"
        );
      } else {
        let { code, mobile, email, name } = this.guestForm;

        let phone = code.value + mobile.value;

        let formBody = new FormData();

        // Object.keys(email.value).length > 0 && alert('Email :: ' + email.value)

        // alert('Phone :: ' + phone)

        formBody.append("phone", phone);
        formBody.append("name", name.value);
        formBody.append("source", 1);
        Object.keys(email.value).length > 0 &&
          formBody.append("email", email.value);

        this.setState({
          number: phone,
        });
        this.props.guestSignup(formBody);
      }
    }
  };

  handleProceed = () => {
    if (!this.state.canSubmit) {
      alert(
        "Please Enter the correct Mobile number and complete name before proceeding"
        // coupon && alert('Coupon in checkout page :: ' + JSON.stringify(coupon))
        //  this.setState({ collapseActive: collapseActive, step3: step3 }, () => {
        // this.state.cart_summary && alert('Cart Summary :: ' + JSON.stringify(this.state.cart_summary))
        // this.props.placeOrder(coupon, this.state.cart_summary);
        //});
      );
    } else {
      let { code, mobile, email, name } = this.guestForm;

      let phone = code.value + mobile.value;

      let formBody = new FormData();

      // Object.keys(email.value).length > 0 && alert('Email :: ' + email.value)

      // alert('Phone :: ' + phone)

      formBody.append("phone", phone);
      formBody.append("name", name.value);
      formBody.append("source", 1);
      Object.keys(email.value).length > 0 &&
        formBody.append("email", email.value);

      this.setState({
        number: phone,
      });
      this.props.guestSignup(formBody, 1);
    }
  };

  handleShortlist = () => {
    this.setState({
      shortlist: !this.state.shortlist,
    });
  };

  handleStepChange = (value) => {
    console.log(value);
    if (localStorage.getItem("jwtToken")) {
      this.setState({
        collapseActive: `${value}`,
        [`step${value}`]: true,
      });
    }
  };

  render() {
    console.log(this.state.cart_summary, "CART SUMMARY");
    let { itemCount } = this.state;
    const cookies = new Cookies();
    let currentCache = JSON.parse(localStorage.getItem("procache"));
    let currentDetail = JSON.parse(localStorage.getItem("cachedProDetail"));

    let me = this.state.me;
    // alert('ME in render :: ' + JSON.stringify(me))

    return (
      <>
        <section id="checkout">
          <Container>
            <Row>
              <Col md={12}>
                <div className="tabs">
                  <Collapse
                    activeKey={this.state.collapseActive}
                    defaultActiveKey={this.state.collapseActive}
                    accordion={true}
                  >
                    <Panel
                      key={1}
                      isActive={true}
                      header="STEP 1: LOGIN"
                      header={
                        <div onClick={() => this.handleStepChange(1)}>
                          <p className="mb-0">STEP 1: LOGIN</p>
                        </div>
                      }
                      headerClass={
                        this.state.step1 === true ? "step1 completed" : "step1"
                      }
                    >
                      <Container>
                        <Row>
                          <Col md={6}>
                            {localStorage.getItem("jwtToken") ? (
                              <div className="already-login">
                                <div className="mb-3">
                                  <p className="email-title">
                                    Your E-mail address:
                                  </p>
                                  <p className="email-txt">
                                    Your email address will be used for order
                                    notification
                                  </p>
                                </div>
                                <div className="mb-3">
                                  <span className="email">
                                    {me && me.cust_email}
                                  </span>
                                  <span className="notu">
                                    {" "}
                                    (Not You?{" "}
                                    <span
                                      className="logout"
                                      onClick={this.handleLogout}
                                    >
                                      Logout
                                    </span>
                                    )
                                  </span>
                                </div>
                                <Button
                                  className="btn-cont"
                                  onClick={() => {
                                    this.setState({
                                      collapseActive: "2",
                                      step2: true,
                                    });
                                  }}
                                >
                                  CONTINUE
                                </Button>
                              </div>
                            ) : (
                              <div className="py-3 px-4">
                                <div className="">
                                  <Button
                                    className="mx-1"
                                    onClick={this.handleShowLogin}
                                  >
                                    Login
                                  </Button>
                                  <Button
                                    className="mx-1"
                                    onClick={() => {
                                      this.setState({
                                        guest: !this.state.guest,
                                      });
                                    }}
                                  >
                                    Login as Guest
                                  </Button>
                                </div>
                                {this.state.guest === true ? (
                                  <div>
                                    <Form
                                      id="guest"
                                      className="guest py-3 px-1"
                                      onSubmit
                                      ref={(guestForm) =>
                                        (this.guestForm = guestForm)
                                      }
                                    >
                                      <Form.Group
                                        as={Row}
                                        controlId="number"
                                        className="mb-3"
                                      >
                                        <Form.Label column sm={3}>
                                          Mobile No : *
                                        </Form.Label>
                                        <Col sm={9}>
                                          <div className="d-flex align-items-center">
                                            <Form.Control
                                              className="mobile-prefix"
                                              type="tel"
                                              maxlength="3"
                                              name="code"
                                              placeholder="+92"
                                              value={"+92"}
                                              disabled

                                              // onChange={this.handleContactChange}
                                            />

                                            <Form.Control
                                              className="mobile-no"
                                              type="tel"
                                              placeholder="e.g : 3321234568"
                                              maxlength="10"
                                              name="mobile"
                                              required
                                              onChange={
                                                this.handleContactChange
                                              }
                                            />
                                          </div>
                                          {/* <Form.Control
                                                                                                    type="tel"
                                                                                                    maxlength="20"
                                                                                                    name="number"
                                                                                                    className="mb-0"
                                                                                                /> */}
                                        </Col>
                                      </Form.Group>
                                      <Form.Group
                                        as={Row}
                                        controlId="name"
                                        className="mb-3"
                                      >
                                        <Form.Label column sm={3}>
                                          Name: *
                                        </Form.Label>
                                        <Col sm={9}>
                                          <Form.Control
                                            type="text"
                                            placeholder="Please enter your name"
                                            autocomplete="off"
                                            maxlength="50"
                                            size="25"
                                            name="name"
                                            className="mb-0"
                                            autocorrect="off"
                                            autocapitalize="off"
                                            onChange={this.handleNameChange}
                                          />
                                        </Col>
                                      </Form.Group>
                                      <Form.Group
                                        as={Row}
                                        controlId="email"
                                        className="mb-3"
                                      >
                                        <Form.Label column sm={3}>
                                          Email: (optional)
                                        </Form.Label>
                                        <Col sm={9}>
                                          <Form.Control
                                            type="email"
                                            autocomplete="off"
                                            maxlength="50"
                                            size="25"
                                            name="email"
                                            className="mb-0"
                                            autocorrect="off"
                                            autocapitalize="off"
                                          />
                                        </Col>
                                      </Form.Group>

                                      {/* { this.state.otp === true ?
                                                                                <>
                                                                                <Form.Control
                                                                                    type="text"
                                                                                    maxlength="6"
                                                                                    name="otp"
                                                                                    placeholder="Enter 6 Digit Verification Code*"
                                                                                    className="mx-1 my-1"
                                                                                /> <Button className="mx-1" >Resend</Button> </>
                                                                                 : <> </>
                                                                                } */}
                                      <p
                                        style={{
                                          color: `${this.state.guest_variant}`,
                                        }}
                                      >
                                        {this.state.guest_msg}
                                      </p>
                                      {this.state.otp === false ? (
                                        <>
                                          <Button
                                            className="mr-4"
                                            onClick={this.submitGuest}
                                            disabled={!this.state.canSubmit}
                                          >
                                            Verify
                                          </Button>
                                          OR
                                          <a
                                            className="ml-4"
                                            href="#"
                                            onClick={this.handleProceed}
                                          >
                                            Skip without verification of mobile
                                          </a>
                                        </>
                                      ) : (
                                        <Button
                                          className="mx-1"
                                          onClick={() => {
                                            this.setState({
                                              collapseActive: "2",
                                              step1: true,
                                            });
                                          }}
                                        >
                                          Login Now
                                        </Button>
                                      )}
                                    </Form>
                                  </div>
                                ) : (
                                  <></>
                                )}
                              </div>
                            )}
                          </Col>
                        </Row>
                      </Container>
                      <span className="info">
                        Babynest Checkout is Simple and Easy. Choose how you
                        want to check out.
                      </span>
                    </Panel>
                    <Panel
                      key={2}
                      isActive={false}
                      header={`STEP 2: ORDER SUMMARY - ${itemCount} Items`}
                      header={
                        <div onClick={() => this.handleStepChange(2)}>
                          <p className="mb-0">
                            {`
                            STEP 2: ORDER SUMMARY - ${itemCount} Items
							  `}
                          </p>
                        </div>
                      }
                      headerClass={
                        this.state.step2 === true ? "step2 completed" : "step2"
                      }
                    >
                      <div id="checkout-cart">
                        <CartDetails
                          {...this.props}
                          handleShortlist={this.handleShortlist}
                          currentCache={currentCache}
                          handlePayment={this.handlePayment}
                          proceedToStep2={this.proceedToStep2}
                          currentDetail={currentDetail}
                          handleValue={this.handleValue}
                          handleTotal={this.handleTotal}
                          saveData={this.props.getCartSummary}
                          zip={this.state.zip}
                          totalValue={
                            this.state.cart_summary &&
                            this.state.cart_summary.totalValue
                          }
                          discount={
                            this.state.cart_summary &&
                            this.state.cart_summary.discount
                          }
                          shipping_charges={
                            this.state.cart_summary &&
                            this.state.cart_summary.shipping_charges
                          }
                          final_payment={
                            this.state.cart_summary &&
                            this.state.cart_summary.final_payment
                          }
                          data={this.state.data && this.state.data}
                          applyCoupon={this.props.applyCoupon}
                          coupon_applied={
                            this.state.coupon_applied &&
                            this.state.coupon_applied
                          }
                          couponMsg={
                            this.state.couponMsg && this.state.couponMsg
                          }
                          coupon_details={
                            this.state.coupon_details &&
                            this.state.coupon_details
                          }
                        />

                        {/* <CartDetails handlePayment={this.handlePayment} currentCache={currentCache} handleValue={this.handleValue} handleTotal={this.handleTotal} saveData={this.saveData} zip={this.state.zip} totalValue={this.state.summary.totalValue} discount={this.state.summary.discount} final_payment={this.state.summary.final_payment} data={this.state.data}  /> */}
                      </div>
                      <span className="info">Let's Review Your Order </span>
                    </Panel>
                    <Panel
                      key={3}
                      isActive={this.state.step1}
                      header={
                        <div onClick={() => this.handleStepChange(3)}>
                          <p className="mb-0">STEP 3: DELIVERY</p>
                        </div>
                      }
                      headerClass={
                        this.state.step2 === true ? "step2 completed" : "step2"
                      }
                    >
                      <Container>
                        <Row>
                          <Col md="6">
                            {me && (
                              <Form
                                id="address"
                                ref={(form) => (this.form = form)}
                              >
                                <Form.Group
                                  as={Row}
                                  controlId="address-name"
                                  className="mb-3"
                                >
                                  <Form.Label column sm={3}>
                                    Name:
                                  </Form.Label>
                                  <Col sm={9}>
                                    <Form.Control
                                      type="text"
                                      name="address-name"
                                      placeholder={me.cust_name}
                                      disabled
                                    />
                                  </Col>
                                </Form.Group>
                                <Form.Group
                                  as={Row}
                                  controlId="house"
                                  className="mb-1"
                                >
                                  <Form.Label column sm={3}>
                                    Flat/House No./ Building:
                                  </Form.Label>
                                  <Col sm={9}>
                                    <Form.Control
                                      type="text"
                                      name="house"
                                      placeholder={
                                        me.cust_address &&
                                        me.cust_address.house_no
                                          ? me.cust_address.house_no
                                          : ""
                                      }
                                      value={
                                        me.cust_address &&
                                        me.cust_address.house_no
                                          ? me.cust_address.house_no
                                          : ""
                                      }
                                      onChange={this.handleAddressChange}
                                    />
                                  </Col>
                                </Form.Group>
                                <Form.Group
                                  as={Row}
                                  controlId="street"
                                  className="mb-1"
                                >
                                  <Form.Label column sm={3}>
                                    Street Address / Colony:
                                  </Form.Label>
                                  <Col sm={9}>
                                    <Form.Control
                                      type="text"
                                      name="street"
                                      placeholder={
                                        me.cust_address &&
                                        me.cust_address.streetAddress
                                          ? me.cust_address.streetAddress
                                          : ""
                                      }
                                      value={
                                        me.cust_address &&
                                        me.cust_address.streetAddress
                                          ? me.cust_address.streetAddress
                                          : ""
                                      }
                                      onChange={this.handleAddressChange}
                                    />
                                  </Col>
                                </Form.Group>
                                <Form.Group
                                  as={Row}
                                  controlId="pincode"
                                  className="mb-3"
                                >
                                  <Form.Label column sm={3}>
                                    Pincode:
                                  </Form.Label>
                                  <Col sm={9}>
                                    <Form.Control
                                      type="text"
                                      name="pincode"
                                      placeholder={
                                        me.cust_zip ? me.cust_zip : ""
                                      }
                                      value={me.cust_zip ? me.cust_zip : ""}
                                      onChange={this.handleAddressChange}
                                    />
                                  </Col>
                                </Form.Group>
                                <Form.Group
                                  as={Row}
                                  controlId="city"
                                  className="mb-3"
                                >
                                  <Form.Label column sm={3}>
                                    City:
                                  </Form.Label>
                                  <Col sm={9}>
                                    <Form.Control
                                      as="select"
                                      name="city"
                                      onChange={this.handleAddressChange}
                                    >
                                      {cities &&
                                        cities.cities.map((city, key) => {
                                          if (
                                            me.cust_city &&
                                            me.cust_city === city
                                          ) {
                                            return (
                                              <option key={key} selected>
                                                {me.cust_city}
                                              </option>
                                            );
                                          } else {
                                            return (
                                              <option key={key}>{city}</option>
                                            );
                                          }
                                        })}
                                    </Form.Control>
                                  </Col>
                                </Form.Group>
                                <Form.Group
                                  as={Row}
                                  controlId="state"
                                  className="mb-3"
                                >
                                  <Form.Label column sm={3}>
                                    State:
                                  </Form.Label>
                                  <Col sm={9}>
                                    <Form.Control
                                      type="text"
                                      name="state"
                                      placeholder={
                                        me.cust_address && me.cust_address.state
                                          ? me.cust_address.state
                                          : ""
                                      }
                                      value={
                                        me.cust_address && me.cust_address.state
                                          ? me.cust_address.state
                                          : ""
                                      }
                                      onChange={this.handleAddressChange}
                                    />
                                  </Col>
                                </Form.Group>
                                <Form.Group
                                  as={Row}
                                  controlId="country"
                                  className="mb-3"
                                >
                                  <Form.Label column sm={3}>
                                    Country:
                                  </Form.Label>
                                  <Col sm={9}>
                                    <Form.Control
                                      type="text"
                                      name="country"
                                      disabled
                                      placeholder={
                                        me.cust_address &&
                                        me.cust_address.country
                                          ? me.cust_address.country
                                          : "Pakistan"
                                      }
                                      value={
                                        me.cust_address &&
                                        me.cust_address.country
                                          ? me.cust_address.country
                                          : "Pakistan"
                                      }
                                      onChange={this.handleAddressChange}
                                    />
                                  </Col>
                                </Form.Group>
                                <Form.Group
                                  as={Row}
                                  controlId="mobile"
                                  className="mb-2"
                                >
                                  <Form.Label column sm={3} className="pr-0">
                                    Mobile No:
                                  </Form.Label>
                                  <Col sm={9}>
                                    <div className="d-flex align-items-center">
                                      <Form.Control
                                        className="mobile-prefix"
                                        type="tel"
                                        maxlength="2"
                                        name="code"
                                        placeholder={
                                          me.cust_phone
                                            ? me.cust_phone.substring(0, 3)
                                            : ""
                                        }
                                        disabled
                                      />
                                      <Form.Control
                                        className="mobile-no"
                                        type="tel"
                                        maxlength="10"
                                        name="Mobile"
                                        placeholder={
                                          me.cust_phone
                                            ? me.cust_phone.substring(3)
                                            : ""
                                        }
                                        disabled
                                      />
                                    </div>
                                  </Col>
                                </Form.Group>
                                <Form.Group
                                  as={Row}
                                  controlId="address-type"
                                  className="mb-3"
                                >
                                  {/* <Form.Label column sm={3}>
                                                                    Address Type:
                                                                </Form.Label> */}
                                  <Col sm={9}>
                                    {/* <div className="mb-2">
                                                                        <Form.Check inline label="Home" type="radio" name="address-type" />
                                                                        <Form.Check inline label="Office" type="radio" name="address-type" />
                                                                    </div> */}
                                    <div>
                                      <Button
                                        className="btn-cont mr-4"
                                        onClick={this.handleAddress}
                                      >
                                        {" "}
                                        USE THIS ADDRESS
                                      </Button>
                                      {/* {this.state.guest_obj && (
                                                                             <>OR <a href="#" className='pl-4' onClick={() => {
                                                                                this.setState ({collapseActive: "3", step2: true })
                                                                             }}>Skip the Address</a></>
                                                                        )} */}
                                      {/* <Button className="btn-clear" type="button">Clear Fields</Button> */}
                                    </div>
                                  </Col>
                                </Form.Group>
                              </Form>
                            )}
                          </Col>
                          <Col md="6"></Col>
                        </Row>
                      </Container>

                      <span className="info">
                        Tell us where you want the item(s) to be delivered
                      </span>
                    </Panel>

                    <Panel
                      key={4}
                      isActive={false}
                      header={
                        <div>
                          <p className="mb-0">STEP 4: PAYMENT</p>
                        </div>
                      }
                      headerClass={
                        this.state.step4 === true ? "step4 completed" : "step4"
                      }
                    >
                      {/* <span>{this.state.orderMessage ? this.state.orderMessage : ''}</span> */}
                      {!this.state.order_summary &&
                        !this.state.paymentComplete && (
                          <div className="m-2">
                            <div className="btn-place-td">
                              <p className="mb-0 font-weight-bold">
                                Select payment method
                              </p>
                              <div className="d-flex mt-2">
                                <div className="d-flex align-items-center mr-4">
                                  <input
                                    checked={
                                      this.state.paymentMethod == 0
                                        ? true
                                        : false
                                    }
                                    onChange={(e) =>
                                      this.setState({ paymentMethod: 0 })
                                    }
                                    type="radio"
                                    className="mr-2"
                                    name="payment-method"
                                  />
                                  <p
                                    className="mb-0"
                                    style={{ fontSize: "0.9rem" }}
                                  >
                                    Cash on delivery
                                  </p>
                                </div>
                                <div className="d-flex align-items-center">
                                  <input
                                    onChange={(e) =>
                                      this.setState({ paymentMethod: 1 })
                                    }
                                    checked={
                                      this.state.paymentMethod == 1
                                        ? true
                                        : false
                                    }
                                    type="radio"
                                    className="mr-2"
                                    name="payment-method"
                                  />
                                  <p
                                    className="mb-0"
                                    style={{ fontSize: "0.9rem" }}
                                  >
                                    Pay Now
                                  </p>
                                </div>
                              </div>
                              <Button
                                onClick={() => {
                                  this.handlePayment(
                                    "4",
                                    true,
                                    this.state.coupon_details &&
                                      this.state.coupon_details
                                  );
                                }}
                                className="btn-order mt-2"
                                variant="success"
                              >
                                PLACE ORDER
                              </Button>
                            </div>
                          </div>
                        )}
                      {this.state.order_summary && (
                        <>
                          <CompletedOrder
                            order_summary={
                              this.state.order_summary &&
                              this.state.order_summary
                            }
                            subTotal={
                              this.state.subTotal && this.state.subTotal
                            }
                          />

                          <span className="info">
                            Last Step and you are done.How would you like to
                            pay?
                          </span>
                        </>
                      )}
                    </Panel>
                  </Collapse>
                </div>
              </Col>
            </Row>
          </Container>
        </section>
        <Login
          handleShowRegister={this.handleShowRegister}
          handleShowLogin={this.handleShowLogin}
          id={this.state.id}
          handleCloseLogin={this.handleCloseLogin}
          show={this.state.showLogin}
          onPopClicked={this.handlePop}
        />
        <OTP number={this.state.number} {...this.props} />
        <Register
          handleShowLogin={this.handleShowLogin}
          handleShowRegister={this.handleShowRegister}
          id={this.state.id}
          handleCloseRegister={this.handleCloseRegister}
          show={this.state.showRegister}
          onPopClicked={this.handlePop}
        />
      </>
    );
  }
}

const mapStateToProps = (state, ownProps) => {
  console.log("JD test :: ", state.checkout.order.couponMsg);
  return {
    itemCount: Math.max(
      state.checkout.cart.itemCount ? state.checkout.cart.itemCount : 0,
      state.checkout.auth.itemCount ? state.checkout.auth.itemCount : 0
    ),
    orderMessage: state.checkout.order.orderMessage,
    order_summary: state.checkout.order.order_summary,
    cart_summary: state.checkout.cart.cart_summary,
    data: state.checkout.cart.data,
    response_message: state.checkout.notification.response_message,
    variant: state.checkout.notification.variant,
    coupon_applied: state.checkout.order.coupon_applied,
    couponMsg: state.checkout.order.couponMsg,
    coupon_details: state.checkout.order.coupon_details,
    shipping: state.checkout.shipping.shipping,
    subTotal: state.checkout.order.subTotal,
    guest_msg: state.checkout.auth.guest_msg,
    guest_variant: state.checkout.auth.guest_variant,
    guest_obj: state.checkout.auth.guest_obj,
  };
};

const mapDispatchToProps = (dispatch, ownProps) => {
  return bindActionCreators(
    {
      saveProductToShortlist: Actions.saveProductToShortlist,
      updateCart: Actions.updateCart,
      updateAddress: Actions.updateAddress,
      placeOrder: Actions.placeOrder,
      Logout: Actions.Logout,
      removeUserData: Actions.removeUserData,
      renderAction: Actions.renderAction,
      getCartSummary: Actions.getCartSummary,
      showResponseMessage: Actions.showResponseMessage,
      applyCoupon: Actions.applyCoupon,
      getShipping: Actions.getShipping,
      guestSignup: Actions.guestSignup,
    },
    dispatch
  );
};

export default withReducer(
  "checkout",
  reducer
)(connect(mapStateToProps, mapDispatchToProps)(CheckoutPage));
